import React, {useEffect} from "react";
import BodyTemplate from "../template/BodyTemplate";
import LinkTemplate from "../template/LinkTemplate";
import './NotFound.css'
import MetaTag from "../template/MetaTag/MetaTag";

export default function NotFound() {
    return (
        <BodyTemplate>
            <MetaTag title={'404 - Not Found'} />
            <h2 style={{textAlign: 'center', marginTop: '2em'}}>URL KHÔNG TỒN TẠI</h2>
            <p style={{textAlign: 'center'}}>Vui lòng quay lại trang chủ để xem thêm về sản phẩm
            </p>
                <LinkTemplate path={'/'}>
                    <div className={'not-found-home'}>
                    TRANG CHỦ
                    </div>
                </LinkTemplate>
        </BodyTemplate>
    );
}
